import { custom, object, string, enum_, optional, union, number, literal, array, boolean } from 'valibot';

import tabOptionStruct from './tab-option-struct'

const toolbarItemStruct = union([literal(false), object({
    icon: string(),
    order: number(),
})], 'false|object')

const contextmenuItemStruct = object({
    title: optional(string()),
    url: optional(string()),
    closable: optional(boolean()),
    disabled: optional(boolean()),
})


const func = custom((value) => typeof value === 'function', {
    type: 'function'
})

export default object({
    minHeight: optional(string()),
    height: optional(string()),
    width: optional(string()),
    lang: optional(string()),
    cacheType: enum_(["local", "session"], '`local`、`session`'),
    responsive: union([object({
        breakpoint: number(),
        hideItem: string(),
    }), literal(false)], 'false|object'),
    defaultTabs: array(tabOptionStruct),
    toolbar: object({
        hide: boolean(),
        position: enum_(['top', 'bottom']),
        prev: toolbarItemStruct,
        refresh: toolbarItemStruct,
        tabWrapper: object({
            order: number(),
        }),
        next: toolbarItemStruct,
        dropdown: toolbarItemStruct,
        fullscreen: toolbarItemStruct,
    }),
    tab: object({
        remember: boolean(),
        contextmenu: union([literal(false), object({
            close: contextmenuItemStruct,
            closeOthers: contextmenuItemStruct,
            closePrev: contextmenuItemStruct,
            closeNext: contextmenuItemStruct,
            closeAll: contextmenuItemStruct,
            fullscreen: contextmenuItemStruct,
            refresh: contextmenuItemStruct,
            centerActive: contextmenuItemStruct,
            newBlank: contextmenuItemStruct,
        })]),
        mouseWheelSwitch: union([literal(false), object({
            onlyScroll: boolean(),
            centerActive: boolean(),
        })]),
        maxNum: number(),
        closeBtn: union([literal(false), object({
            showOnHover: boolean(),
            icon: string()
        })]),
        resizeCenterActive: boolean(),
        clickCenterActive: boolean(),
        doubleClickRefresh: boolean(),
        dragSort: boolean(),
        timeout: union([literal(false), object({
            filter: func,
            text: string(),
            second: number(),
            template: string(),
        })]),
        loading: union([literal(false), object({
            filter: func,
            template: string(),
        })]),
    }),
    onTabActivated: func,
    onTabAddActivated: func,
    onTabLoaded: func,
    onTabTimeout: func,
    onTabFinally: func,
    onTabAll: func,
    onTabLoadingTransitionend: func,
    onTabClick: func,
    onTabDoubleClick: func,
    onTabClose: func,
    onInit: func
})